import util from '@/common/util.js'
export default {
	data() {
		return {
			isCard: true,
			title: "",
			isthumbColor: "#ff5500",
			ThumbColor: "",
			sayList: ""
		}
	},
	onLoad(object) {
		this.title = object.title
		this.sayList = JSON.parse(object.sayList)
		console.log(this.sayList)
		console.log(this.sayList[0].moment)
		console.log(JSON.parse(this.sayList[0].picUrl))
		for(var index = 0; index < this.sayList.length; index++){
			var url = this.sayList[index].picUrl
			this.sayList[index].picUrl = JSON.parse(url)
		}
	},
	methods: {
		// 预览图片函数
		previewImage(idx, picUrl) {
			console.log(idx)
			console.log(picUrl)
			let preview = picUrl;
			uni.previewImage({
				current: idx,
				urls: preview
			});
		},
		getTimeFormat(timeStamp){
			var time = ""
			var date = new Date(parseInt(timeStamp))
			time += date.getFullYear() + "年"
			time += date.getMonth()+1 + "月"
			time += date.getDate() + "日 "
			time += date.getHours()+ ":"
			time += date.getMinutes() + ":"
			time += date.getSeconds()
			return time
		},
		isThumb(textId){
			var flag = null
			uni.request({
			    url: this.Server_IP + 'isThumb', //仅为示例，并非真实接口地址。
			    data: {
					userId: uni.getStorageSync('userId'),
					textId: textId
			    },
			    header: {
			        'custom-header': 'isThumb' //自定义请求头信息
			    },
				method:"POST",
				dataType:"json",
			    success: (res) => {
			        console.log(res.data);
					if(res.data.info.code == '0'){
						if(res.data.data.num == '0'){
							flag =  false
						}
					}else{
						flag = false
					}
			    },
			});
			return flag
		},
		Thumb(index){
			if(this.sayList[index].isThumb == "0"){
				this.addThumb(index)
			}else{
				this.delThumb(index)
			}
		},
		addThumb(index){
			console.log("点赞")
			
			uni.request({
			    url: this.Server_IP + 'addThumb', //仅为示例，并非真实接口地址。
			    data: {
					userId: uni.getStorageSync('userId'),
					textId: this.sayList[index].textId
			    },
			    header: {
			        'custom-header': 'addThumb' //自定义请求头信息
			    },
				method:"POST",
				dataType:"json",
			    success: (res) => {
			        console.log(res.data);
					if(res.data.info.code == '0'){
						uni.showToast({
							icon: 'none',
							title: "点赞成功"
						});
						this.sayList[index].thumb = +this.sayList[index].thumb + 1
						this.sayList[index].isThumb = "1"
					}else{
						uni.showToast({
							icon: 'none',
							title: "点赞失败"
						});
					}
			    },
			});
		},
		delThumb(index){
			console.log("取消点赞")
			
			uni.request({
			    url: this.Server_IP + 'delThumb', //仅为示例，并非真实接口地址。
			    data: {
					userId: uni.getStorageSync('userId'),
					textId: this.sayList[index].textId
			    },
			    header: {
			        'custom-header': 'delThumb' //自定义请求头信息
			    },
				method:"POST",
				dataType:"json",
			    success: (res) => {
			        console.log(res.data);
					if(res.data.info.code == '0'){
						uni.showToast({
							icon: 'none',
							title: "取消成功"
						});
						this.sayList[index].thumb = +this.sayList[index].thumb - 1
						this.sayList[index].isThumb = "0"
					}else{
						uni.showToast({
							icon: 'none',
							title: "取消失败"
						});
					}
			    },
			});
		}
	}
}
